
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { TopUpPage } from '../pages/top-up/top-up';
import { HistoryPage } from '../pages/history/history';
import { ProfileEditPage } from '../pages/profile-edit/profile-edit';
import { PointAccumulationPage } from './../pages/point-accumulation/point-accumulation';
import { CouponPage } from './../pages/coupon/coupon';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AngularFireModule } from 'angularfire2';
import { FIREBASE_CONFIG } from './app.firebase.config';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFireDatabaseModule } from "angularfire2/database";

import { BarcodeScanner } from "@ionic-native/barcode-scanner";
import { LocalNotifications } from '@ionic-native/local-notifications';
import { MessageServiceProvider } from '../providers/message-service/message-service';
import { Geolocation } from '@ionic-native/geolocation';
import { FCM } from '@ionic-native/fcm';
import { NativeStorage } from '@ionic-native/native-storage';


import { HttpModule } from '@angular/http';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicStorageModule } from '@ionic/storage';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}


@NgModule({
  declarations: [
    MyApp,
    TopUpPage,
    ProfileEditPage,
    HistoryPage,
    PointAccumulationPage,
    CouponPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/point-accumulation/point-accumulation.module#PointAccumulationPageModule', name: 'PointAccumulationPage', segment: 'point-accumulation', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/top-up/top-up.module#TopUpPageModule', name: 'TopUpPage', segment: 'top-up', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile-edit/profile-edit.module#ProfileEditPageModule', name: 'ProfileEditPage', segment: 'profile-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/history/history.module#HistoryPageModule', name: 'HistoryPage', segment: 'history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/coupon/coupon.module#CouponPageModule', name: 'CouponPage', segment: 'coupon', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/email-verification/email-verification.module#EmailVerificationPageModule', name: 'EmailVerificationPage', segment: 'email-verification', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/forgot-password/forgot-password.module#ForgotPasswordPageModule', name: 'ForgotPasswordPage', segment: 'forgot-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification/notification.module#NotificationPageModule', name: 'NotificationPage', segment: 'notification', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/top-up-card/top-up-card.module#TopUpCardPageModule', name: 'TopUpCardPage', segment: 'top-up-card', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/top-up-machine/top-up-machine.module#TopUpMachinePageModule', name: 'TopUpMachinePage', segment: 'top-up-machine', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/top-up-machine2/top-up-machine2.module#TopUpMachine2PageModule', name: 'TopUpMachine2Page', segment: 'top-up-machine2', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    HttpModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    TopUpPage,
    ProfileEditPage,
    HistoryPage,
    PointAccumulationPage,
    CouponPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    BarcodeScanner,
    LocalNotifications,
    MessageServiceProvider,
    Geolocation,
    FCM, 
    NativeStorage,
  ]
})
export class AppModule {}



